:root {
  /* --black: #1C6758;
  --blue: #D6CDA4;
  --gray: #EEF2E6; */
  --black: #0f172a;
  --blue: #86e9ee;
  --gray: #94a3b8;
}

* {
  margin: 0;
  padding: 0;
}

html,
body {
  background-color: var(--black);
}

.none {
  display: none !important;
}

#mainbtn {
  width: 20rem;
  height: 20rem;
  background: var(--blue);
  border-radius: 100%;
  padding: 1rem;
  margin: 50vh auto 0;
  transform: translateY(-50%);
  box-shadow: 0 0 10px white;
}

#mainbtn > img {
  cursor: pointer;
  width: 20rem;
  height: 20rem;
  border-radius: 100%;
  transition: width 1s, height 1s;
  display: block;
  margin: auto;
}

.zerosize {
  width: 0 !important;
  height: 0 !important;
}

#options {
  /* position: relative; */
  margin: 0 auto;
  display: flex;
  min-height: 40vh;
}

#options > .option {
  display: inline-block;
  width: 5rem;
  height: 5rem;
  border-radius: 100%;
  /* position: absolute; */
  margin: auto;
  background-color: var(--black);
  color: #fff;
  font-family: sans-serif;
  /* font-size: 2rem; */
  text-align: center;
  line-height: 5rem;
  box-shadow: 0 0 10px var(--gray);
  border: 2px solid #fff;
  cursor: pointer;
}


#content {
  background: var(--gray);
  width: 24rem;
  height: 24rem;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -12rem;
  margin-left: -12rem;
  border-radius: 2rem;
  z-index: 100;
  overflow: auto;
}

#backcontent {
  position: fixed;
  top: 0;
  left: 0;
  opacity: 0.5;
  z-index: 99;
  background: black;
  width: 100%;
  height: 100%;
}

#closewrap {
  direction: rtl;
  padding: 1rem;
  font-size: 2rem;
}

#close {
  cursor: pointer;
}

@media only screen and (max-width: 600px) {
  #content {
    width: 18rem;
    margin-left: -9rem;
  }
}

#about {
  text-align: center;
  font-family: sans-serif;
  color: var(--black);
  width: 90%;
  margin: 0 auto;
  
}

.meImg {
  width: 10rem;
  border-radius: 100%;
}

#about > p {
  text-align: justify;
  font-family: serif;
  font-size: 1.2rem;
}

#skills {
  text-align: center;
  font-family: sans-serif;
  color: var(--black);
  width: 90%;
  margin: 0 auto;
}

#skills > ul {
  width: 50%;
  margin: 0 auto;
}

#skills > ul > li {
  padding: 0.5rem;
}

#work {
  color: var(--black);
  text-align: center;
  font-family: sans-serif;
  width: 90%;
  margin: 0 auto;
}

#head {
  background: black;
  width: 100%;
  padding: 1rem;
  position: fixed;
  top: 0;
  color: white;
}

#changeTheme {
  background: var(--black);
  padding: 1rem;
  font-size: 1.5rem;
  margin-right: 1rem;
  border-radius: 10px;
  color: var(--blue);
  /* margin-left: 70%; */
  cursor: pointer;
}
